/* ----HTML----*/
* {
  box-sizing: border-box;
}

/*----CLEARFIX----*/
.clearfix::after {
  content: " ";
  display: table;
  width: 100%;
  clear: both;
}

/* ----BODY----*/
.page {
  padding: 0;
  font-size: 16px;
  font-family: "OpenSansRegular", sans-serif;
  color: #424242;
  background-color: #ffffff;
}

/* ----CONTAINER---*/
.container {
  max-width: 1024px;
  margin: auto;
}

/*----Header----*/
.header__contact-info {
  /*Если не нужно использовать info-phone_decor-line_vertical*/
  /*(вертикальную линию возле номера телефона)*/
  /*Тогда снять комментарий с padding */
  /*и закомментировать селектор header__info-phone_decor-line_vertical*/
  /*padding: 7px 0 10px 0;*/
  text-align: right;
  background-color: #f0f2f4;
  box-shadow: 0px 2px 4px -2px #b9b9b9;
}

.header__info-phone {
  display: inline-block;
}

@media (max-width: 596px) {
  .header__info-phone {
    padding: 0 5% 0 0;
  }
}

.header__info-phone_decor-line_vertical {
  border-right: 1px solid #d6d6d6;
  height: 40px;
  padding: 9px 12px 0 0;
}

@media (max-width: 596px) {
  .header__info-phone_decor-line_vertical {
    padding: 0;
    border-right: none;
    height: auto;
  }
}

.header__ico-phone {
  vertical-align: -3px;
  font-size: 24px;
  margin: 0 5px 0 0;
}

@media (min-width: 3840px) {
  .header__ico-phone {
    vertical-align: baseline;
  }
}

@media (max-width: 288px) {
  .header__ico-phone {
    font-size: 8vw;
  }
}

.header__info-email {
  padding: 0 3px 0 18px;
  display: inline-block;
}

@media (max-width: 596px) {
  .header__info-email {
    display: none;
  }
}

.header__ico-email {
  font-size: 19px;
}

.header__link {
  padding: 0 7px 0 7px;
  text-decoration: none;
  color: #424242;
  font-size: 15px;
  font-family: "HelveticaRegular", sans-serif;
  transition: 0.5s;
}

.header__link:hover {
  color: #dd2a24;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media (max-width: 1045px) {
  .header__link {
    font-size: 0.87rem;
  }
}

@media (max-width: 288px) {
  .header__link {
    font-size: 5vw;
  }
}

.header__info-social-net {
  display: inline-block;
  margin: 0 1px 0 0;
  border-radius: 12%;
  background-color: #1287bf;
}

@media (max-width: 596px) {
  .header__info-social-net {
    margin: 3% 1% 3% 0;
  }
}

.header__ico-social-net {
  font-size: 17px;
}

.header__link-social-net {
  display: block;
  padding: 5px 8px 6px 8px;
  height: 26px;
  text-decoration: none;
  color: #ffffff;
  transition: 0.5s;
}

.header__link-social-net:hover {
  color: #fbe29d;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media (max-width: 288px) {
  .header__link-social-net {
    display: none;
  }
}

.header__control {
  padding: 46px 0 28px 0;
}

@media (min-width: 3840px) {
  .header__control {
    text-align: center;
  }
}

@media (max-width: 960px) {
  .header__control {
    padding: 9% 0 10% 0;
  }
}

@media (max-width: 288px) {
  .header__control {
    padding: 7% 0 12% 0;
  }
}

/*logo*/
.logo {
  display: inline-block;
  width: 24%;
}

@media (min-width: 3840px) {
  .logo {
    width: 26%;
  }
}

@media (max-width: 1045px) {
  .logo {
    margin: 0;
    width: 28%;
  }
}

@media (max-width: 960px) {
  .logo {
    display: block;
    margin: 0 auto 3% auto;
    text-align: center;
    width: 100%;
  }
}

@media (max-width: 288px) {
  .logo {
    margin: 0 auto 10% auto;
  }
}

.logo__ico {
  margin: 0 11px 0 0;
  position: relative;
  left: 2px;
  bottom: 7px;
  font-size: 57px;
  color: #dd2a24;
  float: left;
}

@media (max-width: 1045px) {
  .logo__ico {
    font-size: 3.4rem;
  }
}

@media (max-width: 960px) {
  .logo__ico {
    float: none;
    margin: 0;
    position: static;
    left: 0;
  }
}

@media (max-width: 288px) {
  .logo__ico {
    font-size: 19vw;
  }
}

.logo__heading {
  margin: 0;
  position: relative;
  bottom: 3px;
  font-size: 37px;
  font-family: "HelveticaBold", sans-serif;
  font-weight: normal;
}

@media (max-width: 1045px) {
  .logo__heading {
    font-size: 2rem;
  }
}

@media (max-width: 960px) {
  .logo__heading {
    letter-spacing: 2px;
  }
}

@media (max-width: 596px) {
  .logo__heading {
    letter-spacing: 0;
  }
}

@media (max-width: 288px) {
  .logo__heading {
    font-size: 12vw;
  }
}

.logo__txt {
  margin: 0;
  padding: 0;
  position: relative;
  bottom: 3px;
  left: 3px;
  font-size: 15px;
  font-family: "HelveticaRegular", sans-serif;
}

@media (max-width: 1045px) {
  .logo__txt {
    font-size: 1rem;
  }
}

@media (max-width: 960px) {
  .logo__txt {
    letter-spacing: 1px;
    left: 0;
  }
}

@media (max-width: 596px) {
  .logo__txt {
    letter-spacing: 0;
  }
}

@media (max-width: 288px) {
  .logo__txt {
    font-size: 5vw;
  }
}

@-moz-document url-prefix() {
  .logo {
    width: 25%;
  }
}

.container__navigation {
  margin: 0 0 0 189px;
}

@media (min-width: 3840px) {
  .container__navigation {
    margin: 0 0 0 25px;
    vertical-align: top;
  }
}

@media (max-width: 1045px) {
  .container__navigation {
    margin: 0 0 0 7%;
  }
}

@-moz-document url-prefix() {
  .container__navigation {
    margin: 0 0 0 184px;
  }
}

.navigation {
  display: inline-block;
}

@media (max-width: 960px) {
  .navigation.container__navigation {
    display: block;
    margin: 0;
    text-align: center;
  }
}

.root[lang=ru] .navigation.container__navigation {
  margin: 0 0 0 46px;
}

@media (max-width: 1045px) {
  .root[lang=ru] .navigation.container__navigation {
    margin: 0;
  }
}

/*Menu*/
.menu {
  margin: 13px 0 0 0;
  padding: 0;
}

.menu__item {
  list-style-type: none;
  display: inline-block;
  vertical-align: 21px;
}

@media (max-width: 960px) {
  .menu__item {
    vertical-align: 0;
  }
}

@media (max-width: 596px) {
  .menu__item {
    display: block;
    margin: auto;
    padding: 0.7% 0 0.7% 0;
    width: 200px;
    border-bottom: 2px solid rgba(66, 66, 66, 0.5);
  }
}

@media (max-width: 288px) {
  .menu__item {
    width: 100%;
    border-bottom: 0;
  }
}

.menu__link {
  text-decoration: none;
  text-transform: capitalize;
  font-family: "HelveticaBold", sans-serif;
  font-size: 15px;
  color: #424242;
  transition: 0.5s;
}

.menu__link:hover {
  color: #dd2a24;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media (max-width: 1045px) {
  .menu__link {
    font-size: 0.9rem;
  }
}

@media (max-width: 960px) {
  .menu__link {
    text-transform: uppercase;
  }
}

@media (max-width: 288px) {
  .menu__link {
    font-size: 5.5vw;
  }
}

.menu__ico {
  margin: 0 14px 0 15px;
  font-size: 6px;
  position: relative;
  bottom: 2px;
  left: 2px;
}

@media (min-width: 3840px) {
  .menu__ico {
    display: none;
  }
}

@media (max-width: 1045px) {
  .menu__ico {
    margin: 0 3px 0 7px;
  }
}

@media (max-width: 960px) {
  .menu__ico {
    margin: 0 8px 0 12px;
  }
}

@media (max-width: 596px) {
  .menu__ico {
    display: none;
  }
}

@media (max-width: 960px) {
  .menu {
    margin: auto;
  }
}

.menu__link[_msthash] {
  letter-spacing: -1px;
}

.container__box-button {
  margin: 4px 0 0 0;
}

@media (min-width: 3840px) {
  .container__box-button {
    margin: 0;
  }
}

@media (max-width: 1045px) {
  .container__box-button {
    float: right;
    margin: 0 2px 0 0;
  }
}

.box-button {
  display: block;
  float: right;
}

@media (max-width: 960px) {
  .box-button {
    display: none;
  }
}

@media (min-width: 5120px) {
  .root[lang=ru] .box-button {
    display: none;
  }
}

/*Button*/
.btn {
  display: inline-block;
  padding: 0 23px 0 23px;
  height: 37px;
  line-height: 36px;
  border-radius: 5px;
  text-decoration: none;
  text-transform: capitalize;
  font-family: "ArialRegular", sans-serif;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: 0.5px;
  color: #ffffff;
  transition: 0.5s;
}

.btn:hover {
  color: #fbe29d;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.btn_theme_bright {
  background-color: #dd2a24;
}

@media (min-width: 5120px) {
  .btn {
    line-height: normal;
    height: auto;
  }
}

@media (min-width: 3840px) {
  .btn {
    padding: 10px 23px 13px 23px;
    line-height: normal;
    height: auto;
  }
}

@media (max-width: 1045px) {
  .btn {
    font-size: 0.9rem;
  }
}

@-moz-document url-prefix() {
  .btn {
    letter-spacing: normal;
  }
}

.btn[_msthash] {
  letter-spacing: -0.5px;
  width: 120px;
  line-height: 100%;
  height: 40px;
  text-align: center;
  padding: 4px 0 0 0;
}

/*----Section Hero----*/
.hero {
  min-height: 590px;
  background: #71796c url("../img/bg-hero.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: cover;
  background-size: cover;
}

.hero__box-info {
  padding: 176px 0 235px 0;
  text-align: center;
}

.hero__heading {
  margin: 0;
  font-family: "HelveticaBold", sans-serif;
  font-size: 48px;
  color: #ffffff;
  font-weight: normal;
}

@media (max-width: 1045px) {
  .hero__heading {
    font-size: 2.7rem;
  }
}

@media (max-width: 288px) {
  .hero__heading {
    font-size: 13vw;
  }
}

.hero__txt {
  margin: 0;
  font-family: "HelveticaLight", sans-serif;
  font-weight: 300;
  font-size: 30px;
  color: #ffffff;
}

@media (max-width: 1045px) {
  .hero__txt {
    font-size: 1.6rem;
  }
}

@media (max-width: 288px) {
  .hero__txt {
    font-size: 7vw;
  }
}

.hero__box-button-big {
  display: inline-block;
  padding: 32px 0 0 0;
}

@media (max-width: 596px) {
  .hero__box-button-big {
    display: block;
  }
}

@media (max-width: 596px) {
  .translated-ltr .hero__heading {
    font-size: 2.3rem;
  }
}

@media (max-width: 288px) {
  .translated-ltr .hero__heading {
    font-size: 12vw;
  }
}

@media (max-width: 240px) {
  .translated-ltr .hero__heading {
    font-size: 10vw;
  }
}

/*Button-Big*/
.btn-big {
  display: inline-block;
  padding: 0 36px 0 36px;
  height: 70px;
  line-height: 74px;
  border-radius: 7px;
  text-decoration: none;
  font-family: "ArialRegular", sans-serif;
  font-weight: 700;
  font-size: 24px;
  white-space: nowrap;
  text-transform: uppercase;
  color: #ffffff;
}

.btn-big_theme_bright {
  background-color: #f8cd53;
  transition: 0.7s;
  transition-property: background-color;
}

.btn-big_theme_bright:hover {
  background-color: #dd2a24;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media (max-width: 1045px) {
  .btn-big {
    font-size: 1.25rem;
  }
}

@media (max-width: 596px) {
  .btn-big {
    padding: 5% 6% 4% 6%;
    font-size: 1.22rem;
    height: auto;
    line-height: normal;
  }
}

@media (max-width: 288px) {
  .btn-big {
    font-size: 6vw;
  }
}

@media (max-width: 596px) {
  .root[lang=ru] .btn-big {
    white-space: normal;
    margin: 0 5px 0 5px;
  }
}

/*----Section About----*/
.about {
  background-color: #ffffff;
}

.about__box-info {
  padding: 104px 0 118px 0;
}

.about__heading {
