//Application стили для сайта landing-business.hw
/*----Section Application----*/
.application {
    background-color: #1287bf;
    &__box-info {
        padding: 100px 0 111px 0; 
        text-align: center;
    }
    &__heading {
       @include title;
       @include bright-txt;

       @media (max-width: $bp-netbook ) {font-size: 2rem;}

       @media (max-width: $bp-smartphone-sm ) {font-size: 12vw;}
    }
    &__txt {
       @include subtitle;
       @include bright-txt;
       margin: 0 0 6px 0; 
       
       @media (max-width: $bp-netbook ) {font-size: 1.07rem;}
       
       @media (max-width: $bp-smartphone-sm ) {font-size: 7vw;}
    }
    &__btn-open {margin: 10px 0 20px 0;}
    &__form {}
}
    //btn-open
@import "app/sass/blocks/btn-open/_btn-open.scss";
    //form
@import "app/sass/blocks/form/_form.scss";