/*tooltip*/
.tooltip {
    display: none;
    padding: 10px;
    width: 450px;
    background: $c-accent;
    font-family: $f-light;
    font-size: 14px;
    letter-spacing: 1px;
    color: $c-light;
    border: 1px solid $c-light;
    box-shadow: 0px 5px 19px 1px $c-dark;
    
    &__txt {margin: 0;}
}
